import { Ref } from 'vue';
import { Size } from '../_utils/constant';

export const VALIDATE_STATUSES = [
  'success',
  'warning',
  'error',
  'validating',
] as const;
export type ValidateStatus = typeof VALIDATE_STATUSES[number];

export const VALIDATE_TRIGGERS = ['change', 'input', 'focus', 'blur'] as const;
export type ValidateTrigger = typeof VALIDATE_TRIGGERS[number];

export interface FieldRule<FieldValue = any> {
  /**
   * @zh  `'string'`
   * @en The type of the value to be checked, the default is `'string'`
   */
  type?:
    | 'string'
    | 'number'
    | 'boolean'
    | 'array'
    | 'object'
    | 'email'
    | 'url'
    | 'ip';
  /**
   * @zh 
   * @en Is it required
   */
  required?: boolean;
  /**
   * @zh 
   * @en Information displayed when verification fails
   */
  message?: string;
  /**
   * @zh string, array
   * @en Check length (string, array)
   */
  length?: number;
  /**
   * @zh string
   * @en Maximum length (string)
   */
  maxLength?: number;
  /**
   * @zh string
   * @en Minimum length (string)
   */
  minLength?: number;
  /**
   * @zh string
   * @en Match check (string)
   */
  match?: RegExp;
  /**
   * @zh string
   * @en Uppercase (string)
   */
  uppercase?: boolean;
  /**
   * @zh string
   * @en Lowercase (string)
   */
  lowercase?: boolean;
  /**
   * @zh number
   * @en Minimum (number)
   */
  min?: number;
  /**
   * @zh number
   * @en Maximum (number)
   */
  max?: number;
  /**
   * @zh number
   * @en Check value (number)
   */
  equal?: number;
  /**
   * @zh number
   * @en Positive number (number)
   */
  positive?: boolean;
  /**
   * @zh number
   * @en Negative number (number)
   */
  negative?: boolean;
  /**
   * @zh  `true`boolean
   * @en Whether it is `true` (boolean)
   */
  true?: boolean;
  /**
   * @zh  `false`boolean
   * @en Whether it is `false` (boolean)
   */
  false?: boolean;
  /**
   * @zh array
   * @en Does the array contain the given value (array)
   */
  includes?: any[];
  /**
   * @zh array
   * @en Are array elements equal (array)
   */
  deepEqual?: any;
  /**
   * @zh object
   * @en Is it empty (object)
   */
  empty?: boolean;
  /**
   * @zh object
   * @en Does the object contain the given attribute (object)
   */
  hasKeys?: string[];
  /**
   * @zh 
   * @en Custom verification rules
   * @param value
   * @param callback
   */
  validator?: (
    value: FieldValue | undefined,
    callback: (error?: string) => void
  ) => void;
}

export interface FieldData {
  /**
   * @zh 
   * @en Field value
   */
  value?: any;
  /**
   * @zh 
   * @en Field status
   */
  status?: ValidateStatus;
  /**
   * @zh 
   * @en Field error message
   */
  message?: string;
}

export interface ValidatedError {
  /**
   * @zh 
   * @en Label text
   * @version 2.18.0
   */
  label: string;
  /**
   * @zh 
   * @en Field name
   */
  field: string;
  /**
   * @zh 
   * @en Field value
   */
  value: any;
  /**
   * @zh 
   * @en Field Type
   */
  type: string;
  /**
   * @zh  `required` 
   * @en Is it a `required` error
   */
  isRequiredError: boolean;
  /**
   * @zh 
   * @en Error message
   */
  message: string;
}

export interface FormItemEventHandler {
  /**
   * @zh onChange
   * @en onChange
   */
  onChange?: (ev?: Event) => void;
  /**
   * @zh onInput
   * @en onInput
   */
  onInput?: (ev?: Event) => void;
  /**
   * @zh onFocus
   * @en onFocus
   */
  onFocus?: (ev?: Event) => void;
  /**
   * @zh onBlur
   * @en onBlur
   */
  onBlur?: (ev?: Event) => void;
}
